.header {
    display: flex;
    align-items: center;
    color: white;
    background-color: #0A192F;
    border-bottom: 5px solid grey;
    border-radius: 2px;
    size: 2px;
    height: 50px;
}

.header_left {
    display: flex;
    flex: 0.4;
}

.header_left>h1 {
    text-align: center;
    font-family: 'Times New Roman', Times, serif;
    letter-spacing: 1px;
    padding: 20px;
}

.header_right {
    display: flex;
    flex: 0.6;
    justify-content: space-evenly;
    align-items: center;
    cursor: pointer;
    padding:20px 0;
    letter-spacing: 1px;
}

.header_right h4:hover{
    background-color: skyblue;
    color: black;
    padding: 5px 20px;
    border-radius: 2px;
    letter-spacing: 1px;
    font-size: 18px;
    cursor:pointer;
}